import React,{useEffect,useState,useContext} from 'react'
import {ProductContext} from '../../pages/oniContext'
export const InputBoxes = () => {
    const pCR = useContext(ProductConsumer)
    return (
      <div>
        {pCR.activeAddOn.map((service, index) => (
          <FormGroup check inline>
            <Label check>
              <Input type="checkbox" /> {service.title}
            </Label>
          </FormGroup>
        ))}
      </div>
    );
}
